import React from 'react';
import { withStyles } from 'material-ui/styles';
import Grid from 'material-ui/Grid';
import Paper from 'material-ui/Paper';
import TextField from 'material-ui/TextField';
import Button from 'material-ui/Button';
import Typography from 'material-ui/Typography';
import { LinearProgress } from 'material-ui/Progress';
import { injectStripe } from 'react-stripe-elements';
import Input, { InputLabel, InputAdornment } from 'material-ui/Input';
import { FormControl, FormHelperText } from 'material-ui/Form';
import CardForm from '../../stripe/cardElement';
import PaymentButton from '../../stripe/paymentRequestElement';

const CreateDonorFormPresentation = ({
  firstName,
  __firstNameError,
  lastName,
  __lastNameError,
  __countryError,
  __cardFieldSelected,
  cardFieldBlur,
  cardFieldFocus,
  cardFieldChange,
  changeHandler,
  submitHandler,
  submitting,
  stripe,
  pendingTotal,
  classes
}) => (
  <div style={{ width: 400 }}>
    <div style={{ padding: 20 }}>
      <Grid container direction="column">
        <Grid item>
          <TextField
            value={firstName}
            error={!!__firstNameError}
            helperText={__firstNameError}
            onChange={changeHandler}
            name="firstName"
            label="First Name"
            fullWidth
          />
        </Grid>
        <Grid item>
          <TextField
            value={lastName}
            error={!!__lastNameError}
            helperText={__lastNameError}
            onChange={changeHandler}
            name="lastName"
            label="Last Name"
            fullWidth
          />
        </Grid>
        <Grid item>
          <CardForm />
          <PaymentButton />
        </Grid>
      </Grid>
    </div>
    {submitting ? (
      <LinearProgress />
    ) : (
      <Button
        variant="raised"
        color="primary"
        onClick={e => submitHandler(e, stripe)}
        style={{ width: '100%', padding: 20 }}
      >
        <Typography variant="title">
          Donate{pendingTotal && ` $${pendingTotal}`}
        </Typography>
      </Button>
    )}
  </div>
);

export default injectStripe(CreateDonorFormPresentation);



