import React from 'react';
import classnames from 'classnames';
import { withStyles } from 'material-ui/styles';
import Card, {
  CardHeader,
  CardMedia,
  CardContent,
  CardActions
} from 'material-ui/Card';
import Collapse from 'material-ui/transitions/Collapse';
import Avatar from 'material-ui/Avatar';
import IconButton from 'material-ui/IconButton';
import Typography from 'material-ui/Typography';
import FavoriteIcon from 'material-ui-icons/Favorite';
import ShareIcon from 'material-ui-icons/Share';
import ExpandMoreIcon from 'material-ui-icons/ExpandMore';
import RedeemIcon from 'material-ui-icons/Redeem';
import DonationModal from '../../donation/donationModal/container';
const styles = theme => ({
  root: { maxWidth: 1200 },
  avatar: {
    backgroundColor: theme.palette.secondary.dark
  },
  icon: { color: theme.palette.secondary.dark }
});
const FundraiserCardPresentation = ({
  _id,
  title,
  owner,
  goal,
  description,
  openDonationModal,
  classes
}) => (
  <Card className={classes.root}>
    <CardHeader
      avatar={
        <Avatar aria-label="Fundraiser" className={classes.avatar}>
          {title.slice(0, 2).toUpperCase()}
        </Avatar>
      }
      action={
        <IconButton
          onClick={() => openDonationModal({ _id, title, owner })}
          className={classes.icon}
        >
          <RedeemIcon />
        </IconButton>
      }
      title={title}
      subheader={`Goal: $${goal}`}
    />
    <CardContent>
      <Typography component="p">{description}</Typography>
    </CardContent>
    <DonationModal />
  </Card>
);

export default withStyles(styles)(FundraiserCardPresentation);



